  div:where(.swal2-container).swal2-center-start > .swal2-popup, div:where(.swal2-container).swal2-center-left > .swal2-popup {
    grid-row: 2;
    align-self: center;
  }
  div:where(.swal2-container).swal2-center > .swal2-popup {
